































































 .ms-category-item {
     overflow: hidden;
     height: 240px;
     width: 47%;
     position: relative;
     margin: 10px 10px;
     cursor: pointer;
     z-index: 10;
     &:hover > &_title{
      text-decoration: underline;
      text-decoration-color: #64b5f6;
      text-decoration-thickness: 3px;
     }
     &_title
     {
         z-index: 5;
         position: absolute;
         padding: 0px 5px;
         margin: 0px;
         /* margin: 10px 10px; */
         width: 100%;
         top: 50%;
         left: 0;
         transform: translateY(-50%);
         color:white;
     }

     &_image {
         background-size: cover;
         overflow: hidden;
         position: absolute;
         top:0;
         left:0;
         z-index: 2;
         width: 100%;
         height: 100%;            
         & > img
         {
             width: 100%;
             height: 100%;
             display: inline-block;
         }
          &:after {
            content:"";
            position:absolute;
            top:0;
            left:0;
            height:100%;
            width:100%;
             background: linear-gradient(to top, rgba(0, 0, 0, 0.85), transparent);
         }
     }
}
