    position: relative;
    resize: both;
    overflow: hidden;
    width: 25px;
    height: 25px;
    display: inline-block;
    border: 1px solid black;
}

.resize > img {
    width: 100%;
    height: 100%;
}

.wysiwyg {
    padding: 15px;
    border: 1px solid #9e9e9e;
    border-radius: 8px;
    background-color: #eeeeee;
    color: #9e9e9e;
    height:400px;
    margin-bottom: 15px;
    overflow-y:scroll;
    &:focus {
       background-color: white;
    }
}

.wysiwyg {
    & ul, ol
    {
        padding-inline-start: 40px;
    }

    & ul {
        list-style: disc !important;
        & > li
        {
            list-style: disc !important;
            text-align:left;
        }
    }
}

.intLink {
    cursor: pointer;
    color: black;
}

img.intLink {
    border: 0;
}

#toolBar1 {
    display: flex;
    justify-content: space-between;
}

#toolBar1 select {
    font-size: 10px;
}

#textBox {
    width: 540px;
    height: 200px;
    border: 1px #000000 solid;
    padding: 12px;
    overflow: scroll;
}

#textBox #sourceText {
    padding: 0;
    margin: 0;
    min-width: 498px;
    min-height: 200px;
}

#editMode label {
    cursor: pointer;
}
