


















































































.ms-select {
    position: relative;
    margin: 16px 0;
    width: 300px;
    font-size: 18px;
    cursor: pointer;
    & .title {
        border: solid 1px #aeaeae;
        padding: 8px 8px;
    }

    & p {
        margin: 0;
    }

    & .options {
        z-index: 20;
        background: #fff;
        border: solid 1px #aeaeae;
        position: absolute;
        top: 45px;
        width: 100%;
        & div {
            padding: 8px 8px;
            &:hover {
                background: #e7e7e7;
            }
        }
    }
}
