













































































































































.ms-calendar {
    position: relative;
    margin: 0 auto;
    &_back {
      position:absolute;
      z-index:10;
      right:0;
      top:0;
    }
    &_description {
        /* Стили для текстового поля с кнопкой "Далее" */
        .limiter {
            max-height: 100px;
            overflow: hidden;
            position: relative;
            & .bottom {
                position: absolute;
                bottom: 0;
                /*background: linear-gradient(to bottom, #fff, #f7f7f7);*/
                width: 100%;
                height: 60px;
                opacity: 1;
                transition: 0.3s;
           }
        }
        .read-more-checker {
            opacity: 0;
            position: absolute;

            &:checked {
                & ~ .limiter {
                    max-height: none;
                    & .bottom {
                        opacity: 0;
                        transition: 0.3s;
                    }
                }
                
                & ~ .read-more-button {
                    &:before {
                        content: "Свернуть \2B9D";
                    }
                }
            }

            & ~ .read-more-button {
                &:before {
                    content: "Развернуть \2B9F";
                }
            }
        }
        .read-more-button {
            cursor: pointer;
            text-align:center;
            display: block;
            color: rgb(30, 87, 165);
            font-size: 14px;
        }
    }
    &_carusel {
      position:relative;
      overflow:hidden;
      flex-direction: row;
      margin-left: 15px;
      margin-right: 15px;
      left: 0px; 
      transition-property: left; 
      transition-duration: 0.3s; 
      transition-timing-function: cubic-bezier(0, 0, 0.12, 0.89);
    }
    &_item {
        margin: 10px;
        width: 30px;
        text-align:center;
        float:left;
        cursor: pointer;
        &:hover {
            border-left:3px solid #64b5f6;
        }
    }
    &_prev {
        z-index: 3;
        background-color: white;
        border-radius:30px;
        box-shadow: 0 0 10px rgba(0,0,0,0.5);
        padding: 1px 10px;
        cursor:pointer;
    }
    &_next {
        z-index: 3;
        box-shadow: 0 0 10px rgba(0,0,0,0.5);
        padding: 1px 10px;
        background-color: white;
        border-radius:30px;
        cursor:pointer;
    }
}
